.checkoutProduct{
    /* everything going into a row with flex */
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
}
.checkoutProduct_image{
    object-fit: contain;
    width: 180px;
    height: 180px;
}

.checkoutProduct_info{
    padding-left:20px;
}
.checkoutProduct_title{
    font-size: 17px;
    font-weight: 800;
}

/* dont think i need this, watchout */
.checkoutProduct_rating{
    display: flex;
}

.checkoutProduct_info > button{
    background-color:#f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
}


